/* cinzel-400normal - latin */
@font-face {
  font-family: 'Cinzel';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src:
    local('Cinzel Regular '),
    local('Cinzel-Regular'),
    url('./files/cinzel-latin-400.woff2') format('woff2'), /* Super Modern Browsers */
    url('./files/cinzel-latin-400.woff') format('woff'); /* Modern Browsers */
}

